import { MyLoading, MyUI } from "@macashipo/mlib"
import React from "react"
import { MyModal } from "src/core/components"
import styled from "styled-components"

const WrapCard = styled.div`
  margin-bottom: 0.5rem;
  border-radius: 2px;
  padding: 0.5rem;
  & .a-body {
    border: 1px solid #81b0e4;
    border-radius: 4px;
    padding: 15px;
    padding-bottom: 5px;
    text-align: left;
    background: #eff6ff;
    position: relative;
    margin-top: 1rem;
    height: 100%;
    & .a-body-label {
      position: absolute;
      top: -10px;
      background: #4190e7;
      color: white;
      border: 1px solid #81b0e4;
      border-radius: 4px;
      padding: 0px 5px;
    }
  }
  & .a-note {
    border: 1px solid #e4e181;
    border-radius: 4px;
    padding: 15px;
    padding-bottom: 5px;
    text-align: left;
    background: #fffeef;
    margin-top: 1rem;
    position: relative;
    & .a-note-label {
      position: absolute;
      top: -10px;
      background: white;
      border: 1px solid #e4e181;
      border-radius: 4px;
      padding: 0px 5px;
    }
  }
  & .a-code,
  .a-name {
    display: flex;
    align-items: center;
  }
  & .a-field {
    background: #27394d;
    border-radius: 3px;
    padding: 0px 2px;
    color: white;
    font-size: 10px;
    width: 40px;
    text-align: center;
    margin-right: 10px;
  }
  & .a-empty {
    font-style: italic;
    color: #cdcdcd;
  }
`
class MC_card_row_email_template extends React.Component {
  render() {
    const data = this.props.data || {}
    return (
      <WrapCard
        className="card"
        style={{
          width: "100%",
        }}
      >
        <div
          className="a-code"
          onClick={() => {
            MyModal.showCellInModal(
              {},
              {
                title: "Code",
              }
            )
          }}
        >
          <span className="a-field">Code</span>
          {data.Code ? (
            <span>{data.Code}</span>
          ) : (
            <span className="a-empty">Empty Code</span>
          )}
        </div>
        <div
          className="a-name"
          onClick={() => {
            MyModal.showCellInModal(
              {},
              {
                title: "Name",
              }
            )
          }}
        >
          <span className="a-field">Name</span>
          {data.Name ? (
            <span>{data.Name}</span>
          ) : (
            <span className="a-empty">Empty Name</span>
          )}
        </div>
        {/* <div className="a-subject">{data.Subject}</div> */}
        {data.Body && (
          <div className="a-body">
            <div className="a-body-label">{data.Subject || "No Subject"}</div>
            <MyUI type="html" html={data.Body} />
          </div>
        )}
        {data.Note && (
          <div className="a-note">
            <div className="a-note-label">Note</div>
            <div>{data.Note}</div>
          </div>
        )}
        <MyUI type={"_wrapEditRowInCard"} />
      </WrapCard>
    )
  }
}

export default MC_card_row_email_template
